.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 100px;
    color: var(--text-color);
}

.title {
    font-size: 100px;
}

.subtitle {
    margin-top: 10px;
}

.divider {
    border-bottom: 6px solid var(--text-color);
    width: 100px;
    height: 0px;
}

@media only screen and (max-width: 600px) {
    .container {
        margin-left: 30px;
    }

    .divider {
        border-bottom: 2px solid var(--text-color);
        width: 20px;
        height: 0px;
    }

    .subtitle {
        font-size: 10px;
        margin-top: 10px;
    }

    .title {
        font-size: 20px;
    }
}