body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --text-color: rgb(255, 239, 239);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  font-family: 'PT Serif', serif;
  height: 100%;
  background-image: -webkit-linear-gradient(left, #068ede, #9ce5f1);
  background-image: linear-gradient(to right, #068ede, #9ce5f1);
}

body div {
  height: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}


.nav-link {
    background-color: transparent;
    color: var(--text-color);
    font-weight: 600;
    width: 150px;
    border: none;
    text-decoration: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.nav-link:focus {
    outline: none;
}

.nav-link:hover {
    cursor: pointer;
    background-color: rgba(20, 103, 226, 0.14);
}

.nav-link:active {
    cursor: pointer;
    background-color: rgba(20, 103, 226, 0.45);
}


@media only screen and (min-width: 600px) {
    .mobile {
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    .nav-link {
        font-weight: 400;
        font-size: 15px;
        width: 50px;
        border: none;
    }
}
